import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { DepotApi, DepotReveiveApi } from 'utils/Api';

const Shop = () => {
  const { t } = useTranslation();
  const [presonData, setPresonData] = useState([]);
  useEffect(() => {
    localStorage.setItem('navBar', 'depot');
    DepotApi().then((res) => {
      setPresonData(res.data);
    });
  }, []);

  const handleClick = (item) => {
    if (item.reveive !== item.done) {
      DepotReveiveApi(item).then(() => {
        window.location.reload();
      });
    }
  };
  return (
    <div className=" flex flex-col ">
      {presonData.map((item, index) => (
        <div
          key={index}
          className="flex flex-col mt-2 ml-4 mr-4 rounded-xl border-solid  shadow-lg shadow-gray-200"
        >
          <p className=" ml-auto text-white text-sm text-center w-28 rounded-tr-xl rounded-bl-xl   bg-orange-500">
            {item.product.period}
            {t('hour')}
            {t('settlement')}
          </p>

          <div className="flex mt-2 justify-around items-center">
            <div className="flex flex-col  items-center m-1">
              <p className="font-bold text-2xl text-orange-500">
                {item.product.income}
              </p>
              <p className="text-xs text-gray-400">{t('time_income')}</p>
            </div>

            <div className="flex flex-col  items-center">
              <p className="font-bold text-2xl text-orange-500">
                {item.income * item.done}
              </p>
              <p className="text-xs text-gray-400">{t('cumulative_income')}</p>
            </div>

            <div className="flex flex-col  items-center">
              <p className="font-bold text-2xl text-orange-500">
                {item.income * item.product.period}
              </p>
              <p className="text-xs text-gray-400">{t('expected_income')}</p>
            </div>

            <div className="flex flex-col  items-center">
              <p className="font-bold text-2xl text-orange-500">{item.price}</p>
              <p className="text-xs text-gray-400">{t('lease_fees')}</p>
            </div>
          </div>

          <div className="flex justify-start items-center h-12 mt-2 mb-3">
            <img
              alt="logo"
              src={process.env.REACT_APP_SERVER + item.product.imageUrl}
              className="bg-red-100 m-2 h-full w-12"
            />
            <div className="flex flex-col justify-between gap-2 ">
              <p className="font-bold text-sm text-black">
                {item.product.name}
              </p>
              <div className="flex justify-between gap-4">
                <p className="text-orange-600">{}</p>
              </div>
            </div>
            <button
              className="rounded-2xl border-orange-500 border-2 p-2 pl-6 pr-6 text-orange-500 ml-auto mr-4"
              onClick={() => handleClick(item)}
            >
              {item.reveive !== item.done ? t('reveive_income') : t('sharing')}
            </button>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Shop;
